
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  margin-top: 0px;
}
.btn-margin-top {
  margin-top: 10px;
}
.image-in-list {
  height: 100%;
  width: 150px
}
.item-in-list {
  width: 150px;
  height: 100%
}
.item-margin-top-list {
  margin-top: 10px;
  border-bottom-style: inset !important;
}
.content-cover {
  padding: 20px 40px;
}
.error-msg {
  margin: 0px;
  color: #c1150a
}



